import React from 'react'
import { properties } from '../Properties/Properties.js'
import BaseSurveyComponent from "./BaseSurveyComponent"
import CategoriesComponent from "./CategoriesComponent"
import MainMenuComponent from "./MainMenuComponent"
import MapChart from './MapChart.js'
import { useParams } from "react-router-dom";

import Grid from '@material-ui/core/Grid';


export default function SeparateSurveyComponent(props) {

    const [state, setState] = React.useState({
        isLoading: true,
        survey: {}
    })

    React.useEffect(() => {
        fetch(`${properties.address}/survey?id=${props.SurveyId}`, { method: 'GET', credentials: 'include' })
            .then(response => {
                if (response.status === 200)
                    return response.json()
            })
            .then(result => setState({ ...state, survey: result, isLoading: false }))
    }, [])
    return <>
        {state.survey != null && !state.isLoading && <BaseSurveyComponent surveyJson={state.survey} />}</>
}