import React from 'react';
import ReactDOM from 'react-dom';
import "./index.css";
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Hidden from '@material-ui/core/Hidden';

import TabsComponent from './Components/TabsComponent'
import CategoriesComponent from './Components/CategoriesComponent'
import MainMenuComponent from './Components/MainMenuComponent'
import LoginComponent from './Components/LoginComponent'
import SignUpComponent from './Components/SignUpComponent'
import AddSurveyComponent from './Components/AddSurveyComponent'
import ConfirmRegistration from './Components/ConfirmRegistration'
import { BrowserRouter as Router, Route } from 'react-router-dom'
import { Redirect } from "react-router-dom";
import ProfileComponent from "./Components/ProfileComponent";
import { properties } from './Properties/Properties.js';
import { Link } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import RandomPageComponent from "./Components/Pages/RandomPageComponent";
import SurveyPageComponent from "./Components/Pages/SurveyPageComponent";
import NewsPageComponent from "./Components/Pages/NewsPageComponent";
import { createMuiTheme } from '@material-ui/core/styles';
import { grey } from '@material-ui/core/colors';
import AboutPageComponent from "./Components/Pages/AboutPageComponent";
import DonatePageComponent from "./Components/Pages/DonatePageComponent";


import { ThemeProvider } from '@material-ui/core/styles';
import PopupComponent from "./Components/PopupComponent"
import Fade from '@material-ui/core/Fade';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      <Link color="inherit" href="/home">
        runningwilder.github.io/SurveyIt
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

export const UserContext = React.createContext({})
export const PopupContext = React.createContext("help me")
export const LoginContext = React.createContext(() => { })

function App() {
  const [state, setState] = React.useState({
    loggedIn: false,
    username: "",
    userId: 0,
    activeType: "",
    activeTag: "",
    message: "",
    hidden: true,
    userInfo: {
      username: null,
      userId: null,
      loggedIn: null
    }
  })

  const checkLoginStatus = () => {
    console.log("checking auth")
    fetch(`${properties.address}/logged_in`,
      {
        method: 'GET',
        credentials: 'include',
      })
      .then(response => {
        if (response.status === 200 && !state.loggedIn) {
          return response.json()
        }
        else {
          setState({
            ...state, userInfo: {
              username: null,
              userId: null,
              loggedIn: false
            }
          })
        }
      })
      .then(result => {
        if (result) {
          console.log(result)
          setState({
            ...state, userInfo: {
              username: result.username,
              userId: result.user_id,
              loggedIn: true
            }
          })
        }

      })
  }

  const setMessageFunc = msg => {
    setState({ ...state, message: msg, hidden: false })
    setTimeout(() => {
      setState({ ...state, message: msg, hidden: true })
    }, 3000)
  }

  React.useEffect(() => {
    checkLoginStatus()
  }, [])

  return (
    <LoginContext.Provider value={checkLoginStatus}>
      <UserContext.Provider value={state.userInfo}>
        <PopupContext.Provider value={setMessageFunc}>
          <Router>
            <Grid container spacing={3}>
              <Grid item xs={12} sm={9} >
                <Box>
                  <Fade in={!state.hidden} timeout={500}>
                    <div>
                      <PopupComponent message={state.message} />
                    </div>
                  </Fade>
                  <Route exact={true} path={[`${properties.repo}/`, `${properties.repo}/new`, `${properties.repo}/home`, `${properties.repo}/top`]}><TabsComponent /></Route>
                  <Route path={`${properties.repo}/random`} render={props => <RandomPageComponent {...props} />} />
                  <Route path={`${properties.repo}/login`}><LoginComponent handleLoggedIn={checkLoginStatus} /></Route>
                  <Route path={`${properties.repo}/createSurvey`}><AddSurveyComponent /></Route>
                  <Route path={`${properties.repo}/profile`}><ProfileComponent /></Route>
                  <Route path={`${properties.repo}/register`}><SignUpComponent /></Route>
                  <Route path={`${properties.repo}/registrationConfirm`}><ConfirmRegistration /></Route>
                  <Route path={`${properties.repo}/survey/:id`}><SurveyPageComponent /></Route>
                  <Route path={`${properties.repo}/news`}><NewsPageComponent /></Route>
                  <Route path={`${properties.repo}/donate`}> <DonatePageComponent /> {/* <SendFeedbackComponent /> */} </Route>
                  <Route path={`${properties.repo}/about`}><AboutPageComponent /></Route>
                  {/* <Route path={`${properties.repo}/signout`}>{ history.push(`${properties.repo}/`)} </Route> */}
                  <Route path={`${properties.repo}/signout`}><Redirect to={`${properties.repo}/`} /></Route>
                  <Copyright />
                </Box>
              </Grid>
              {/* </Grid> */}
              <Grid item sm={3} style={{ paddingRight: "10px" }}>
                <Hidden only="xs">
                  <Box style={{ position: "sticky", top: "0" }}>
                    <CategoriesComponent />
                  </Box>
                  <MainMenuComponent />
                </Hidden>
              </Grid>
            </Grid>
          </Router>
        </PopupContext.Provider>
      </UserContext.Provider>
    </LoginContext.Provider >
  );
}

const christmasTheme = createMuiTheme({

  overrides: {
    MuiListItem: {
      root: {
        "&$selected": {
          // backgroundColor: grey[800],
          background: "linear-gradient(21deg, rgba(54,54,61,0.8132440476190477) 0%, rgba(95,154,164,0.7740283613445378) 100%)"
        },
        "&$selected:hover": {
          backgroundColor: grey[700],
        },
      },
    }
  },
  shape: {
    borderRadius: 0,
  },
  palette: {
    primary: {
      main: "#ccced8"
    },
    secondary: {
      main: "#3d608c"
    },
    info: {
      main: "#54c0ef8c"
    }
    // type: "dark"
  },
  typography: {
    fontSize: 13,
    // fontFamily: "Palatino Linotype, Book Antiqua, serif",
    // fontFamily: "Lucida Sans Unicode, Lucida Grande, serif"
    // fontFamily: "Lucida Console, Monaco, monospace"
    fontFamily: "Comic Sans MS, cursive, sans-serif"
  }
});

ReactDOM.render(<ThemeProvider theme={christmasTheme}><App /></ThemeProvider>,
  document.getElementById("root"));