import React from 'react';
import { useParams } from 'react-router-dom'

import CircularProgress from '@material-ui/core/CircularProgress'
import { properties } from '../../Properties/Properties.js';
import BaseSurveyComponent from "../BaseSurveyComponent";
import MapChart from '../MapChart.js'
import { PopupContext } from '../../index'
import CommentsComponent from "../CommentsComponent"
import { useHistory } from "react-router-dom";

export default function SeparateSurveyComponent(props) {
    const { id: surveyId } = useParams()
    const [survey, setSurvey] = React.useState()
    const setMessageFunc = React.useContext(PopupContext)
    const history = useHistory()

    React.useEffect(() => {
        fetch(`${properties.address}/survey?id=${surveyId}`,
            {
                method: 'GET',
                credentials: 'include',
            })
            .then(response => {
                if (response.status === 200)
                    return response.json()
            })
            .then(result => {
                if (result)
                    setSurvey(result)
            })
    }, [])
    const onSurveyDeleteFunc = e => {
        history.push(`${properties.repo}/profile`)
    }
    return <>
        {!survey && <CircularProgress />}
        {survey && <BaseSurveyComponent surveyJson={survey} onSurveyDeleteFunc = {onSurveyDeleteFunc} />}

        <div style={{ width: "500px", height: "200px", border: "1px solid black" }}>
            <MapChart markers={survey && survey.locations} />
        </div>
        {/* <CommentsComponent /> */}
    </>
}