import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Typography, Grid } from '@material-ui/core';

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import Avatar from '@material-ui/core/Avatar';
import FolderIcon from '@material-ui/icons/Folder';

import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import CircularProgress from '@material-ui/core/CircularProgress';
import CropOriginalIcon from '@material-ui/icons/CropOriginal';
import { Link } from 'react-router-dom';
import { properties } from '../Properties/Properties.js';

const useStyles = makeStyles((theme) => ({
}))

export default function ProfileComponent(props) {
    const classes = useStyles()
    const [open, setOpen] = React.useState(false)
    const [surveyToDelete, setsurveyToDelete] = React.useState()
    const [surveys, setSurveys] = React.useState([])

    const handleClose = () => {
      setOpen(false);
    };

    const {UserId, Username} = props
    
    const handleDelete = e => {
      fetch(`${properties.address}/deleteSurvey`, 
      {
        method: 'DELETE', 
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({surveyId: surveyToDelete.id}),
        redirect: 'follow',
        credentials: 'include',
      })
      .then(response => {
        if (response.status === 200){
          setSurveys(surveys.filter(survey => survey !== surveyToDelete))
        }
      })
    }
    
    const handleOpen = surveyToDelete => {
      setsurveyToDelete(surveyToDelete)
      setOpen(true);
    }
    React.useEffect(() => {
      
      if (UserId !== 0){
          fetch(`${properties.address}/getProfileSurveys`, 
          {
            method: 'GET',
            credentials: 'include',
          })
          .then(response => {
            if (response.status === 200){
              return response.json()
            }
          }).then(result => {
            if (result){
                setSurveys(result)
            }
          })
        }
    }, [UserId])
        
    return(<>
      <Grid container>
        <Grid item xs = {12}>
          <Typography variant = "h5">
            {Username}
          </Typography>
          <Typography variant = "h6">
            Ваши опросы:
          </Typography>
          <List dense={true}>
            {surveys.length === 0 && <CircularProgress />}
            {surveys.length !== 0 && surveys.map(survey =>
              <ListItem button>
                <ListItemAvatar>
                  <Avatar>
                    {survey.image &&<img src = {survey.image} style = {{maxWidth: "50px"}}></img>}
                    {!survey.image && <CropOriginalIcon></CropOriginalIcon>}
                  </Avatar>
                </ListItemAvatar>
                <Link  style={{ textDecoration: 'none', width: "100%"}} to = {`${properties.repo}/survey/${survey.id}`}>
                  <ListItemText
                    primary={survey.question}
                    secondary={survey.answered_amount + " проголосовавших"}
                  />
                </Link>
                <ListItemSecondaryAction>
                  <IconButton edge="end" onClick = {e => {handleOpen(survey)}}>
                    <DeleteIcon />
                  </IconButton>
                </ListItemSecondaryAction>
              </ListItem>
            )}
          </List>
        </Grid>
      </Grid>
      <Dialog
        open={open}
        onClose={handleClose}
      >
        <DialogTitle>{"Вы действительно хотите удалить этот опрос?"}</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Удаляя этот опрос вы также лишаете других пользователей возможности голосовать и просматривать его статистику, которая может быть значимой для других.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Отменить
          </Button>
          <Button onClick={e => {handleClose(); handleDelete()}} color="primary" autoFocus>
            Удалить
          </Button>
        </DialogActions>
      </Dialog>
    </>)
}