import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import { Link } from 'react-router-dom'
import Grid from '@material-ui/core/Grid';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import FormHelperText from '@material-ui/core/FormHelperText';
import { properties } from '../Properties/Properties.js';
import MenuItem from '@material-ui/core/MenuItem';
import Card from '@material-ui/core/Card';
// import { FixedSizeList } from 'react-window';



const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function SignUpComponent() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    name: '',
    password: '',
    email: '',
    matchingPassword: '',
    gender: '',
    age: '',
    country: '',
    errors: {
      serverErrors: false,
      clientErrors: false
    },
    succesMessage: ""
  })
  const [gender, setGender] = React.useState("male")
  const [countries, setCountries] = React.useState([])

  React.useEffect(() => {
    fetch(`${properties.address}/getCountries`,
      {
        method: 'GET',
        redirect: 'follow',
        // credentials: 'include'
      })
      .then(response => {
        if (response.status === 200) {
          return response.json()
        }
      })
      .then(result => {
        if (result) {
          setCountries([{ name: "", id: 0 }, ...result])
        }
      })
  }, [])

  const handleOnChange = e => {
    setState({ ...state, [e.target.name]: e.target.value })
  }
  const validate = () => {
    let errs = []
    if (state.age < 0) {
      errs.push("На данный момент регистрация пользователей с возрастом меньше нуля лет не предусмотрена :(")
    }
    if (state.age > 150) {
      errs.push("Рекомендуем выключите экран дабы не усугублять проблемы со зрением. В вашем возрасте они наверняка есть")
    }
    if (state.country === "") {
      errs.push("Выберите страну")
    }
    if (state.gender === "") {
      errs.push("Укажите пол")
    }
    return errs
  }
  const handleSubmit = e => {
    e.preventDefault()

    fetch(`${properties.address}/signUp`,
      {
        method: 'POST',
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          "name": state.name,
          "password": state.password,
          "email": state.email,
          "matchingPassword": state.matchingPassword,
          "country": state.country,
          "age": state.age,
          "gender": state.gender,
        }),
        redirect: 'follow',
        credentials: 'include'
      })
      .then(response => {
        if (response.status === 400) {
          response.json().then(result => {

            setState({
              ...state, errors: {
                ...state.errors,
                serverErrors: result,
                clientErrors: validate()
              }
            })
          })
        }
        else if (response.status === 200) {
          setState({
            ...state,
            succesMessage: "Регистрация успешна! Для продолжения перейдите по ссылке присланной на указанную почту"
          })
        }
        return response.json()
      })
  }
  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Card style={{ padding: "20px" }}>
          <Typography component="h1" variant="h5">
            Sign up
        </Typography>
          <Typography component="h2">
            {state.succesMessage}
          </Typography>
          <form className={classes.form} noValidate >
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  autoComplete="login"
                  name="name"
                  variant="outlined"
                  required
                  fullWidth
                  id="login"
                  label="Login"
                  defaultValue={state.name}
                  onChange={handleOnChange}
                // autoFocus
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  name="gender"
                  select
                  variant="outlined"
                  required
                  fullWidth
                  id="gender"
                  label="Пол"
                  defaultValue={"none"}
                  onChange={handleOnChange}
                  SelectProps={{ native: true }}
                >
                  <option value="" ></option>
                  <option value="male" >Мужской</option>
                  <option value="female" >Женский</option>
                </TextField>
              </Grid>
              <Grid item xs={12}>
                <TextField
                  inputProps={{
                    max: 150,
                    min: 0
                  }}
                  name="age"
                  variant="outlined"
                  required
                  fullWidth
                  id="age"
                  type="number"
                  label="Возраст"
                  defaultValue={state.age}
                  onChange={handleOnChange}
                // autoFocus
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  select
                  name="country"
                  variant="outlined"
                  required
                  fullWidth
                  id="country"
                  label="Страна"
                  // native
                  // defaultValue = {state.country}
                  onChange={handleOnChange}
                  SelectProps={{ native: true }}
                // autoFocus
                >
                  {/* <FixedSizeList height={400} width={300} itemSize={46} itemCount={200}> */}
                  {countries.map(country =>
                    <option key={`country-${country.id}`} value={country.name} >{country.name}</option>
                  )}
                  {/* </FixedSizeList> */}

                </TextField>
              </Grid>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  id="email"
                  label="Email Address"
                  name="email"
                  autoComplete="email"
                  defaultValue={state.email}
                  onChange={handleOnChange}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  name="password"
                  label="Password"
                  type="password"
                  id="password"
                  autoComplete="current-password"
                  defaultValue={state.password}
                  onChange={e => { handleOnChange(e); }}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  name="matchingPassword"
                  label="Confirm your password"
                  type="password"
                  id="confirm_password"
                  autoComplete="current-password"
                  defaultValue={state.matchingPassword}
                  onChange={e => { handleOnChange(e); }}
                />
                <FormHelperText error={true}>
                  {state.matchingPassword !== state.password ? "Пароли не совпадают" : ""}
                </FormHelperText>

                {Array.from(state.errors.clientErrors).map(el =>
                  <FormHelperText error={true}>
                    {el}
                  </FormHelperText>
                )}
                {Array.from(state.errors.serverErrors).map(el =>
                  <FormHelperText error={true}>
                    {el}
                  </FormHelperText>
                )}
              </Grid>
              {/* <Grid item xs={12}>
              <FormControlLabel
                control={<Checkbox value="allowExtraEmails" color="primary" />}
                label="I want to receive inspiration, marketing promotions and updates via email."
              />
            </Grid> */}
            </Grid>

            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
              onClick={handleSubmit}

            >
              Sign Up
          </Button>
            <Grid container justify="flex-end">
              <Grid item>
                <Link to={`${properties.repo}/login`} variant="body2">
                  Уже есть аккаунт? Войти
              </Link>
              </Grid>
            </Grid>
          </form>
        </Card>
      </div>
      {/* <Box mt={5}>
        <Copyright />
      </Box> */}
    </Container>
  );
}