import React, { Fragment } from "react";
import request from "superagent";
import debounce from "lodash.debounce";
import { useRef } from 'react'
import { properties } from '../Properties/Properties.js';
import BaseSurveyComponent from "./BaseSurveyComponent"
import { PopupContext } from '../index'
import { Typography } from "@material-ui/core";

export default function InfiniteScroll(props) {
  const setMessageFunc = React.useContext(PopupContext)
  const ref = useRef(null)
  const [state, setState] = React.useState({
    error: false,
    hasMore: true,
    isLoading: true,
    surveys: [],
    index: 0,
  })
  const onSurveyDeleteFunc = e => {
    // alert('Удалено :)')
  }
  const load = (tag, type, clearSurveys) => {
    if (clearSurveys)
      setState({ ...state, surveys: [] })
    let url = props.Mode === 'Top' ? `${properties.address}/surveys-top?index=` : `${properties.address}/surveys?index=`

    url += state.index
    tag !== undefined ? url += "&tag=" + tag : url += "&tag="
    type !== undefined ? url += "&type=" + type : url += "&type="
    request
      .get(url)
      .withCredentials()
      .then((results) => {
        const new_list = results.body.map(s =>
          <BaseSurveyComponent surveyJson={s} onSurveyDeleteFunc={onSurveyDeleteFunc} />)
        setState({
          ...state,
          hasMore: (state.surveys.length < 100) && results.body.length !== 0,
          isLoading: false,
          surveys: [
            ...state.surveys,
            ...new_list
          ],
          index: state.index + 1
        })
      })
  }

  window.onscroll = debounce(() => {
    if (state.error || state.isLoading || !state.hasMore) return;
    // Checks that the page has scrolled to the bottom
    if (ref.current &&
      window.innerHeight + document.documentElement.scrollTop
      > ref.current.offsetTop + ref.current.clientHeight
    ) {
      setState({ ...state, isLoading: true })
      load(undefined, props.ActiveType)
    }
  }, 300)

  React.useEffect(() => {
    load(undefined, props.ActiveType)
    if (props.SetReloadFunction) {
      props.SetReloadFunction(load)
    }
  }, [])

  return <>
    <div ref={ref}>
      {/* {props.Mode} */}
      {state.surveys.map((survey, index) => (
        <Fragment key={survey.props.ElementId}>
          {survey}
          <br />
        </Fragment>
      ))}
      <hr />
      {state.error &&
        <div style={{ color: '#900' }}>
          {state.error}
        </div>
      }
      {state.isLoading && <div>Loading...</div>}
      {!state.hasMore && <Typography>Вы сделали это! Вы дошли до конца, так держать!</Typography>}
    </div>
  </>
}