import React from 'react'
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Divider from '@material-ui/core/Divider';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import DeleteOutlineOutlinedIcon from '@material-ui/icons/DeleteOutlineOutlined';
import Collapse from '@material-ui/core/Collapse';
import { properties } from '../Properties/Properties.js';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import CardContent from '@material-ui/core/CardContent';
import { makeStyles } from '@material-ui/core/styles';
import { PopupContext } from '../index'
import { UserContext } from '../index'

const useStyles = makeStyles((theme) => ({
    avatar: {
        backgroundColor: "#41465f",
    }
}));

export default function CommentsComponent(props) {
    const userInfo = React.useContext(UserContext)
    const setMessageFunc = React.useContext(PopupContext)
    const classes = useStyles()
    const [state, setState] = React.useState({
        commentError: "",
        comment: "",
        comments: props.surveyJson.comments,
    })
    const handleCommentSubmit = e => {
        e.preventDefault()

        if (state.comment.length < 5 || state.comment.length > 200) {
            setState({ ...state, commentError: "Не меньше 5 и не больше 200 символов" })
            return
        }
        setState({ ...state, commentError: "" })

        fetch(`${properties.address}/addComment`,
            {
                headers: {
                    'Content-Type': 'application/json'
                },
                method: 'POST',
                credentials: 'include',
                body: JSON.stringify({
                    content: state.comment,
                    surveyId: props.surveyJson.id
                })
            })
            .then(response => {
                if (response.status == 200)
                    return response.json()
            })
            .then(result => {
                if (!result)
                    return
                console.log(result)
                let newComments = state.comments
                let date = new Date(result.date)
                newComments.push({
                    "content": state.comment,
                    "likes": 0,
                    "user_id": result.user_id,
                    "username": "admin",
                    "date": `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`,
                    "id": result.id,
                })

                setState({ ...state, comments: newComments })
                if (props.setCommentsLength)
                    props.setCommentsLength(newComments.length)
            })

    }

    const handleCommentLike = comment => {

        if (userInfo.userId === null) {
            setMessageFunc("Авторизуйтесь, пожалуйста :с")
            return
        }

        comment.likes = comment.voted ? comment.likes - 1 : comment.likes + 1
        comment.voted = !comment.voted
        setState({ ...state, comments: state.comments })

        fetch(`${properties.address}/commentLike`,
            {
                headers: {
                    'Content-Type': 'application/json'
                },
                method: 'POST',
                credentials: 'include',
                body: JSON.stringify({ commentId: comment.id })
            })
            .then(response => {
                if (response.status == 200) {
                    return response.json()
                }
                else {
                    comment.likes = comment.voted ? comment.likes - 1 : comment.likes + 1
                    comment.voted = !comment.voted
                    setState({ ...state, comments: state.comments })
                }
            }).then(result => {
                if (result) {
                    // new_comments = comments.find(comment -> comment.id === commentId)
                    comment.likes = result.likes
                    comment.voted = result.voted
                    setState({ ...state, comments: state.comments })
                }
            })
    }

    const handleDeleteComment = commentId => {
        fetch(`${properties.address}/deleteComment`,
            {
                method: 'DELETE',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    commentId: commentId
                }),
                redirect: 'follow',
                credentials: 'include',
            })
            .then(response => {
                if (response.status === 200) {
                    let newComments = state.comments.filter(comment => comment.id !== commentId)
                    setState({ ...state, comments: newComments })
                    if (props.setCommentsLength)
                        props.setCommentsLength(newComments.length)
                    setMessageFunc("Комментарий удален :)")
                }
            })
    }

    return <Collapse style={{ borderTop: "1px solid #e0e0e0" }} in={props.expanded} timeout="auto" unmountOnExit>
        <CardContent>
            <List>
                {state.comments.map(comment => <>
                    <ListItem style={{ padding: 0, paddingLeft: 10 }} alignItems="flex-start">
                        <ListItemAvatar>
                            <Avatar style={{ width: 30, height: 30 }} variant="rounded" aria-label="" className={classes.avatar} >
                                {comment.username && comment.username.charAt(0)}
                            </Avatar>
                        </ListItemAvatar>

                        <ListItemText
                            primary={<>
                                {comment.username + "  "}
                                <Typography variant="caption" component="span" color="textSecondary">
                                    {comment.date}
                                </Typography>
                            </>}
                            secondary={
                                <React.Fragment>
                                    <div style={{ display: "flex", wordBreak: "break-word" }}>

                                        <Typography
                                            component="span"
                                            variant="body2"
                                            style={{ display: "inline" }}
                                            color="textPrimary"
                                        >
                                            {comment.content}
                                        </Typography>
                                        <div style={{ marginLeft: "auto" }}>
                                            {/* {comment.user_id === props.LoggedInUserId && <IconButton className={classes.icon} onClick={e => handleDeleteComment(comment.id)}>
                            <DeleteOutlineOutlinedIcon />
                          </IconButton>} */}
                                            {userInfo.userId === props.surveyJson.userId && <IconButton className={classes.icon} onClick={e => handleDeleteComment(comment.id)}>
                                                <DeleteOutlineOutlinedIcon />
                                            </IconButton>}
                                            <Typography component="span">
                                                {comment.likes != 0 && comment.likes}
                                            </Typography>

                                            <IconButton color={comment.voted ? "secondary" : "primary"} className={classes.icon} aria-label="add to favorites" onClick={e => { handleCommentLike(comment) }}>
                                                <ThumbUpAltIcon />
                                            </IconButton>
                                        </div>
                                    </div>

                                </React.Fragment>
                            }
                        />
                    </ListItem>
                    <Divider variant="inset" component="li" />
                </>)}
            </List>
            <FormControl style={{ display: "flex" }}>
                <form onSubmit={handleCommentSubmit}>
                    <TextField
                        style={{ display: "flex" }}
                        id="standard-textarea"
                        label="Комментировать"
                        placeholder=""
                        multiline
                        onBlur={e => { setState({ ...state, comment: e.target.value }); e.target.value = "" }}
                    />
                    {state.commentError && <FormHelperText error={true}>{state.commentError}</FormHelperText>}
                    <Button onClick={handleCommentSubmit} type="submit" variant="contained" >Отправить</Button>
                </form>
            </FormControl>
        </CardContent>
    </Collapse>

}