import React from 'react'
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';

import FormatListBulletedIcon from '@material-ui/icons/FormatListBulleted';
import ViewListIcon from '@material-ui/icons/ViewList';
import AppsIcon from '@material-ui/icons/Apps';

import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Button from '@material-ui/core/Button';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import TextField from '@material-ui/core/TextField';
import Tooltip from '@material-ui/core/Tooltip';
import IconButton from '@material-ui/core/IconButton';
import RadioButtonUncheckedIcon from '@material-ui/icons/RadioButtonUnchecked'
import ClearIcon from '@material-ui/icons/Clear';
import Select from '@material-ui/core/Select';
import NativeSelect from '@material-ui/core/NativeSelect';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import TagsComponent from './TagsComponent.js';
import { properties } from '../Properties/Properties.js';
import MenuItem from '@material-ui/core/MenuItem';
import { useHistory } from "react-router-dom";

import MultipleSurveyComponent from "./MultipleSurveyComponent";
import SingleSurveyComponent from "./SingleSurveyComponent";
import RatingSurveyComponent from "./RatingSurveyComponent";

import Container from '@material-ui/core/Container';
import CssBaseline from '@material-ui/core/CssBaseline';
import CardComponent from "./CardComponent";
import BaseSurveyComponent from "./BaseSurveyComponent"
import { PopupContext } from '../index'

const useStyles = makeStyles((theme) => ({
  paper: {
    // marginTop: theme.spacing(8),
    // display: 'flex',
    // flexDirection: 'column',
    // alignItems: 'center',
  },
  kinds: {
    backgroundColor: "#33a570",
  },
  mainBox: {
    backgroundColor: "#33a570",
    padding: 10
  },
}))
export default function AddSurveyComponent(props) {
  let history = useHistory();
  const classes = useStyles()
  const [surveyName, setSurveyName] = React.useState("")
  const [error, setError] = React.useState(false)
  const [errors, setErrors] = React.useState({})
  const [categories, setCategories] = React.useState([])
  const [tags, setTags] = React.useState([])
  const [selectedValue, setSelectedValue] = React.useState("None")
  const [state, setState] = React.useState({
    options: new Set(),
    rows: new Set(),
    columns: new Set(),
    type: 'Single',
    body: null,
    image: ""
  })
  const setMessageFunc = React.useContext(PopupContext)
  const handleTags = tags => {
    setTags(tags)
  }
  React.useEffect(() => {
    fetch(`${properties.address}/getCategories`,
      {
        method: 'GET',
        redirect: 'follow',
        credentials: 'include',
      })
      .then(response => {
        if (response.status === 200) {
          return response.json()
        }
      })
      .then(result => {
        // let resultWithKeys = [...result]
        // resultWithKeys = resultWithKeys.map(item => {return {...item, key: item.id}})
        setCategories([{ name: "None", id: -1 }, ...result])
        // setCategories([...result])
      })
  }, [])

  const handleSelect = e => {
    setSelectedValue(e.target.value)
  }
  const changeHandler = e => {
    setSurveyName(e.target.value)
  }
  const handleSubmit = e => {
    e.preventDefault()
    if (Object.keys(validateForm()).length === 0) {
      let body = {
        'question': surveyName,
        'type': state.type,
        'image': state.image,
        'category': selectedValue,
        'tags': tags
      }
      if (state.type === 'Rating') {
        body = {
          ...body,
          'rows': [...state.rows],
          'columns': [...state.columns],
        }
      }
      else {
        body = {
          ...body,
          'options': [...state.options]
        }
      }

      fetch(`${properties.address}/addSurvey`,
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(body),
          redirect: 'follow',
          credentials: 'include',
        })
        .then(response => {
          if (response.status == 200) {
            return response.json()
          }
        })
        .then(result => {
          if (result){
            setMessageFunc("Опрос создан :)")
            history.push(`${properties.repo}/new`)
          }
        })
    }
  }

  const handleOnBlur = (event) => {
    if (event.target.value !== "") {
      let opts = state.options
      opts.add(event.target.value)
      setState({ ...state, options: opts })
      event.target.value = ""
    }
  }

  const handleChange = (event, index, name) => {
    let opts = [...state[name]]
    opts[index] = event.target.value
    setState({ ...state, [name]: new Set(opts) })
  }
  const handleBlur = (event) => {
    if (event.target.value !== "") {
      let set = state[event.target.name]
      set.add(event.target.value)
      setState({
        ...state,
        [event.target.name]: set
      })
      event.target.value = ""
    }
  }
  const handleDelete = (name, option) => {
    let i = [...state[name]].indexOf(option)
    let opts = [...state[name]]
    opts.splice(i, 1)
    setState({
      ...state,
      [name]: new Set(opts)
    })
  }

  const handleOnChange = (event, index, name) => {
    let opts = [...state.options]
    opts[index] = event.target.value
    setState({
      ...state,
      options: new Set(opts)
    })
  }
  const handleOnDelete = option => {
    let i = [...state.options].indexOf(option)
    let opts = [...state.options]
    opts.splice(i, 1)
    setState({ ...state, options: new Set(opts) })
  }
  const renderForm = (name, aim) => {
    return <>
      {Array.from(state[name]).map((row, index) =>
        <Grid key={row} container alignItems="center">
          <Grid item xs={1}>
            {index + 1}.
              </Grid>
          <Grid item xs={9}>
            <TextField defaultValue={row} onBlur={(e) => handleChange(e, index, name)} />
          </Grid>
          <Grid item xs={2}>
            <IconButton onClick={(e) => handleDelete(name, row)} color="primary" aria-label="" component="span">
              <ClearIcon />
            </IconButton>
          </Grid>
        </Grid>
      )}
      <TextField name={name} placeholder={aim} onBlur={handleBlur} />
    </>
  }
  const renderBody = e => {
    switch (state.type) {
      case 'Single':
        return <>
          {Array.from(state.options).map((option, index) =>
            <Grid key={option} container spacing={1} alignItems="flex-end">
              <Grid item>
                <RadioButtonUncheckedIcon color="disabled" />
              </Grid>
              <Grid item>
                <TextField defaultValue={option} onBlur={(e) => handleOnChange(e, index)} />
              </Grid>
              <Grid item >
                <IconButton onClick={() => handleOnDelete(option)} color="primary" aria-label="" component="span">
                  <ClearIcon />
                </IconButton>
              </Grid>
              <Grid item >
              </Grid>
            </Grid>
          )}
          <Grid container spacing={1} alignItems="flex-end">
            <Grid item>
              <RadioButtonUncheckedIcon color="disabled" />
            </Grid>
            <Grid item>
              <TextField placeholder="Добавить вариант" onBlur={handleOnBlur} />
            </Grid>
          </Grid>
          <FormHelperText>{errors.Options}</FormHelperText>
        </>
      case 'Multiple':
        return <>
          {Array.from(state.options).map((option, index) =>
            <Grid key={option} container spacing={1} alignItems="flex-end">
              <Grid item>
                <CheckBoxOutlineBlankIcon color="disabled" />
              </Grid>
              <Grid item>
                {/* <TextField  value = {option} onChange = {(e) => handleOnChange(e, index)}/> */}
                <TextField defaultValue={option} onBlur={(e) => handleOnChange(e, index)} />
              </Grid>
              <Grid item >
                <IconButton onClick={() => handleOnDelete(option)} color="primary" aria-label="" component="span">
                  <ClearIcon />
                </IconButton>
              </Grid>
              <Grid item >
              </Grid>
            </Grid>
          )}
          <Grid container spacing={1} alignItems="flex-end">
            <Grid item>
              <CheckBoxOutlineBlankIcon color="disabled" />
            </Grid>
            <Grid item>
              <TextField placeholder="Добавить вариант" onBlur={handleOnBlur} />
            </Grid>
          </Grid>
          <FormHelperText>{errors.Options}</FormHelperText>
        </>
      case 'Rating':
        return <>
          <Grid container spacing={1}>
            <Grid item xs={6}>
              {renderForm("rows", "Добавить строку")}
            </Grid>
            <Grid item xs={6}>
              {renderForm("columns", "Добавить столбец")}
            </Grid>
          </Grid>
          <FormHelperText>{errors.Columns}</FormHelperText>
          <FormHelperText>{errors.Rows}</FormHelperText>
        </>
      default:
        return <div>Происходит что-то странное. Не знаешь что именно?</div>
    }
  }
  const validateForm = () => {
    let errors = {}
    if (surveyName === '') {
      errors.Name = 'Введите имя'
    }
    if (selectedValue === "" || selectedValue === undefined || selectedValue === "None") {
      errors.Select = 'Выберите категорию'
    }
    switch (state.type) {
      case 'Multiple':
      case 'Single':
        if (state.options.size < 2) {
          errors.Options = 'Создайте более одного варианта'
        }
        break;
      case 'Rating':
        if (state.rows.size < 2) {
          errors.Rows = 'Добавьте более одного рядка'
        }
        if (state.columns.size < 2) {
          errors.Columns = 'Добавьте более одной колонки'
        }
        break;
      default:
        errors.Type = 'Тип опроса не определен'

    }

    setErrors(errors)
    setError(Object.keys(errors).length > 0 ? true : false)
    return errors
  }
  const handleClick = body_type => {
    setState({ ...state, type: body_type })
  }
  const imageHandler = e => {
    setState({ ...state, image: e.target.value })
  }
  return (<>
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <Paper className={classes.kinds}>
          <TextField
            autoComplete="off"
            placeholder="Новый опрос"
            onBlur={changeHandler}
            // value={surveyName}
            required
            error={errors.Name == null ? false : true}
          />

          <Tooltip title="Единичный выбор">
            <IconButton onClick={() => handleClick('Single')} color={state.type === 'Single' ? 'primary' : 'default'}>
              <ViewListIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="Множественный выбор">
            <IconButton onClick={() => handleClick('Multiple')} color={state.type === 'Multiple' ? 'primary' : 'default'}>
              <FormatListBulletedIcon />
            </IconButton>
          </Tooltip>
          <Tooltip title="Рейтинговый выбор">
            <IconButton onClick={() => handleClick('Rating')} color={state.type === 'Rating' ? 'primary' : 'default'}>
              <AppsIcon />
            </IconButton>
          </Tooltip>
        </Paper>
      </Grid>
      <Grid item xs={12}>
        <Grid container spacing={3}>
          <Grid item xs={6} >
            <Paper className={classes.mainBox}>
              <form onSubmit={handleSubmit}>
                <FormControl
                  component="fieldset"
                  error={error}
                >
                  <FormHelperText>{errors.Name}</FormHelperText>
                  <TextField
                    autoComplete="off"
                    placeholder="Ссылка на изображение (не обязательно)"
                    onBlur={imageHandler}
                    error={errors.img == null ? false : true}
                  />
                  <FormControl required  >
                    <InputLabel htmlFor="age-native-required">Категория</InputLabel>
                    <Select
                      value={selectedValue}
                      onChange={handleSelect}
                      name="category"
                    >
                      {categories.map(c => <MenuItem key={c.id} value={c.name}>{c.name}</MenuItem>)}
                    </Select>
                  </FormControl>

                  {renderBody()}

                  <FormHelperText>{errors.Select}</FormHelperText>

                  <TagsComponent CallBack={handleTags}></TagsComponent>
                  <br></br>
                  <Button type="submit" variant="contained" color="primary" onClick={handleSubmit}>Добавить опрос</Button>
                </FormControl>
              </form>
            </Paper>
          </Grid>
          <Grid item xs={6}>
            <BaseSurveyComponent
              surveyJson={{
                type: state.type,
                comments: [],
                tags: [],
                options: Array.from(state.options),
                rows: Array.from(state.rows),
                columns: Array.from(state.columns)
              }} />
            {/* <CardComponent
              AnsweredAmount={0}
              Voted={0}
              ElementId={0}
              Username={props.UserName}
              Likes={0}
              Answered={0}
              Tags={tags}
              Comments={[]}
              UserId={props.UserId}
              Date={"now"}
              LoggedInUserId={props.UserId}
              LoggedInUsername={props.UserName}
            >
              {state.type === "single" && <SingleSurveyComponent
                Image={state.image}
                Voted={false}
                Question={surveyName}
                Options={Array.from(state.options)}
                SurveyId={0} />
                ||
              state.type === "multiple" && <MultipleSurveyComponent
                Image={state.image}
                Voted={false}
                Question={surveyName}
                Options={Array.from(state.options)}
                SurveyId={0} />
                ||
              state.type === "rating" && <RatingSurveyComponent
                Image={state.image}
                Voted={false}
                Question={surveyName}
                Rows={Array.from(state.rows)}
                Columns={Array.from(state.columns)}
                SurveyId={0} />}
            </CardComponent> */}

          </Grid>
        </Grid>
      </Grid>
    </Grid>
  </>)
}