import React, { useState, useEffect } from 'react';
import ReactMarkdown from 'markdown-to-jsx'
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import Typography from '@material-ui/core/Typography';
import { Paper } from '@material-ui/core';
import MainMenuComponent from "../MainMenuComponent"
import Grid from '@material-ui/core/Grid';
import SeparateSurveyComponent from "../SeparateSurveyComponent"

const useStyles = makeStyles((theme) => ({
    markdown: {
        ...theme.typography.body2,
        padding: theme.spacing(3, 0),
    },
}));
const renderers = {
    overrides: {
        p: { component: Typography, props: { paragraph: true } },
        li: { component: Typography, props: { variant: "subtitle1", component: "li" } },
        h4: { component: Typography, props: { variant: "h6" } },
        SeparateSurveyComponent: { component: SeparateSurveyComponent }
    }
}
export default function AboutPageComponent(props) {
    const classes = useStyles();
    const [posts, setPosts] = useState([])
    const importAll = r => r.keys().map(r);

    const markdownFiles = importAll(require.context('../../about', false, /\.md$/))
        .sort()
        .reverse()

    React.useEffect(() => {
        async function fetchData() {
            const posts = await Promise.all(markdownFiles.map((file) => fetch(file).then((res) => res.text())))
                .catch((err) => console.error(err))
            setPosts(posts)
        }
        fetchData()
    }, [])

    return (
        <>
            <Card>
                {posts.map((post, idx) => (
                    <>
                        <ReactMarkdown options={renderers}>
                            {post}
                        </ReactMarkdown>
                        <hr style={{ borderTop: "1px dotted black", backgroundColor: "transparent", height: "1px" }} />
                    </>))}
            </Card>
        </>


    )
}