import React from 'react';
import Box from '@material-ui/core/Box';
import { makeStyles } from '@material-ui/core/styles';
import RemoveCircleOutlineIcon from '@material-ui/icons/RemoveCircleOutline';
import IconButton from '@material-ui/core/IconButton';
import Input from '@material-ui/core/Input';
import Popper from '@material-ui/core/Popper';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import { properties } from '../Properties/Properties.js';

const useStyles = makeStyles(() => ({
  root: {
    border: "2px solid #426480",
    borderRadius: 5,
    '&:hover': {
      outline: "none",
      borderColor: "#5f7d96",
      boxShadow: "0 0 5px #5f7d96",
    },
    padding: 3,
    display: 'flex',
  },
  tag: {
    borderRadius: 7,
    padding: 0,
    backgroundColor: "#a0a9da",
    paddingLeft: 10,
    fontSize: 15,
    width: "max-content",
    margin: 2
  },
  icon: {
    padding: 2
  },
  input: {
    marginLeft: 5,
  },
  popper: {
    padding: 3,
    width: 200
  },
  fetchedTag: {
    backgroundColor: "#a2a2dc",
    margin: 2,
    padding: 5,
    borderRadius: 5,
    cursor: "pointer",
    '&:hover': {
      backgroundColor: "#c3c3ec",
    },
    display: "inline-block"
  },
  h1: {
    width: 30,
    marginTop: -16,
    marginLeft: 5,
    // background: "white",
    height: "fit-content",
    fontSize: 18,
    fontWeight: "normal"
  }

}))

export default function TagsComponent(props) {
  const classes = useStyles()
  const [tags, setTags] = React.useState([])
  const [value, setValue] = React.useState("")
  const [anchorEl, setAnchorEl] = React.useState(null)
  const [fetchedTags, setFetchedTags] = React.useState([])

  const open = Boolean(anchorEl)
  const id = open ? 'simple-popover' : undefined
  React.useEffect(() => {
    props.CallBack(tags)
  }, [tags])
  const addTag = name => {
    handleClose()
    setTags([...tags, name])
    setFetchedTags([...fetchedTags].filter(el => el.name !== name))
    setValue("")
  }
  const handleClick = (e, index) => {
    tags.splice(index, 1)
    setTags([...tags])
  }
  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleChange = e => {
    setAnchorEl(e.target.value ? e.currentTarget : null);
    setValue(e.target.value)
    fetch(`${properties.address}/getTags?text=${e.target.value}`,
      {
        method: 'GET',
        redirect: 'follow',
        credentials: 'include',
      })
      .then(response => {
        if (response.status === 200) {
          return response.json()
        }
      })
      .then(result => {
        setFetchedTags(result.filter(el => !tags.some(tag => tag === el.name)))
      })
  }

  return <>
    <Typography>
      Теги (добавлять свои теги пока что нельзя)
    </Typography>
    <Box
      className={classes.root}
    >
      {/* <h1 className = {classes.h1}>Test</h1> */}
      {tags.map((tag, index) =>
        <Box className={classes.tag}>
          {tag}
          <IconButton onClick={e => handleClick(e, index)} className={classes.icon} color="primary">
            <RemoveCircleOutlineIcon />
          </IconButton>
        </Box>)}
      <Input
        className={classes.input}
        onChange={handleChange}
        value={value}
        error={false}
      >
      </Input>
      <Popper
        open={open}
        id={id}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
      >
        <Paper elevation={2} className={classes.popper}>
          {fetchedTags.length > 0 && fetchedTags.map(tag => <>
            <Box className={classes.fetchedTag} onClick={() => addTag(tag.name)}>
              {tag.name}
            </Box>
          </>)}
          {fetchedTags.length === 0 && <div>Ой. Этот тег еще не придумали</div>}
        </Paper>
      </Popper>
    </Box>
  </>
}