import React from 'react';

import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import Button from '@material-ui/core/Button';
import Checkbox from '@material-ui/core/Checkbox';
import FormGroup from '@material-ui/core/FormGroup';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';
import DefaultTooltipContent from 'recharts/lib/component/DefaultTooltipContent';
import { KeyPressedContext } from './Pages/RandomPageComponent'

export default function SingleSurveyComponent(props) {
  props = props.props
  let keyUpEvent = React.useContext(KeyPressedContext)
  const [state, setState] = React.useState({
    error: false,
    voted: props.surveyJson.answered,
    options: {},
    helperText: props.HelperText,
    chart: undefined
  })

  const handleSubmit = e => {
    if (e)
      e.preventDefault()
    let submitOptions = {
      "surveyId": props.surveyJson.id,
      "choosedOptions": Object.keys(state.options).filter(key => state.options[key] === true)
    }
    props.handleSubmit(e, submitOptions, validate, processDataFunc)
  }

  const processDataFunc = (e, result) => {
    let longestOption = result.map(elem => elem.name).sort((e1, e2) => e2.length - e1.length)[0].length
    let choosedOptions = props.surveyJson.choosedOptions || Object.keys(state.options).filter(key => state.options[key] === true)
    let chart = <BarChart width={20 * longestOption + 300} height={40 * result.length} data={result} layout="vertical">
      <XAxis type="number" hide />
      <YAxis
        tick={props => {
          return <text
            x={props.x}
            y={props.y}
            textAnchor="end"
            fontWeight={choosedOptions.includes(props.payload.value) ? "bold" : "normal"}
            fill={choosedOptions.includes(props.payload.value) ? "blue" : "black"}
          >
            {props.payload.value + " (" + result.filter(r => r.name === props.payload.value)[0].value + "%)"}
          </text>
        }}
        type="category" dataKey="name" width={20 * longestOption} />
      <Tooltip content={props => {
        if (props.payload[0] != null) {
          const newPayload = [{
            name: 'голосов',
            value: props.payload[0].payload.count
          }]
          return <DefaultTooltipContent {...props} payload={newPayload} />
        }
        return <DefaultTooltipContent {...props} />
      }} />
      <Bar dataKey="value" fill="#8884d8" />
    </BarChart>
    setState({ ...state, chart: chart })
  }

  const validate = () => {
    if (Object.values(state.options).filter(value => value === true).length === 0) {
      setState({ ...state, helperText: 'Выберите вариант', error: true })
      return false
    }
    setState({ ...state, helperText: props.HelperText, error: false })
    return true
  }

  const handleChange = (event) => {
    setState({ ...state, options: { ...state.options, [event.target.name]: event.target.checked } })
  }

  React.useEffect(() => {
    if (props.Answered)
      props.getStats(undefined, processDataFunc)
  }, [props.Answered])

  React.useEffect(() => {
    if (keyUpEvent)
      keyUpEvent = keyUpEvent.keyCode
    if (keyUpEvent >= 49 && keyUpEvent <= 49 + Object.keys(props.surveyJson.options).length - 1) {
      let newValue = !state.options[props.surveyJson.options[keyUpEvent - 49]]
      setState({ ...state, options: { ...state.options, [props.surveyJson.options[keyUpEvent - 49]]: newValue } })
    }
    if (keyUpEvent === 13) {
      handleSubmit()
    }
  }, [keyUpEvent])

  React.useEffect(() => {
    // setState({ ...state, options: {}, error: false })
    setState({ ...state, error: false })
  }, [])

  return <FormControl
    component="fieldset"
    error={state.error}
  >
    {/* {props.flipped &&
      <StatisticsComponent
        Options={props.surveyJson.options}
        Type="Multiple"
        SurveyId={props.surveyJson.surveyId}>
      </StatisticsComponent>} */}
    {!props.flipped && <form onSubmit={handleSubmit}>
      <FormLabel component="legend">{props.surveyJson.question}</FormLabel>
      <FormGroup>
        {!props.Answered &&
          props.surveyJson.options.map((option) =>
            <FormControlLabel
              checked={state.options[option] == true}
              control={<Checkbox name={option} onChange={handleChange} />}
              label={option}
            />
          )
        }
      </FormGroup>
      <FormHelperText>{state.helperText}</FormHelperText>
      {!props.Answered && <Button type="submit" variant="contained" color="primary">Узнать</Button>}
      {props.Answered && state.chart}
    </form>}
    {/* {props.Answered && <StatisticsComponent />} */}

  </FormControl>
}