import React, { Children, cloneElement } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import CheckIcon from '@material-ui/icons/Check';
import Tooltip from '@material-ui/core/Tooltip';
import LoopIcon from '@material-ui/icons/Loop';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { properties } from '../Properties/Properties.js';
import { Link } from 'react-router-dom';
import { UserContext } from '../index'
import CommentsComponent from "./CommentsComponent"
import { PopupContext } from '../index'


const useStyles = makeStyles((theme) => ({
  header: {
    padding: 0,
    backgroundColor: theme.palette.primary.main,
  },
  expand: {
    transform: 'rotate(0deg)',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: "#41465f",
  },
  tag: {
    margin: 2,
    backgroundColor: "#a2a2dc",
    margin: 2,
    borderRadius: 5,
    cursor: "pointer",
    '&:hover': {
      backgroundColor: "#c3c3ec",
    },
    fontSize: 15,
    color: "#2f4f4f",
    paddingLeft: 5,
    paddingRight: 5,
    fontFamily: theme.typography.fontFamily
  },
  action: {
    margin: 0
  }
}));

export default function CardComponent(props) {
  const userInfo = React.useContext(UserContext)
  const classes = useStyles()
  const setMessageFunc = React.useContext(PopupContext)
  const [state, setState] = React.useState({
    answered: props.surveyJson.answered,
    answeredAmount: props.surveyJson.answered_amount,
    likes: props.surveyJson.likes,
    voted: props.surveyJson.voted,
    flipped: true,
    anchorEl: null,
    expanded: false,
    commentsCount: props.surveyJson.comments.length
  })

  const handleFlip = e => setState({ ...state, flipped: !state.flipped })
  const handleExpandClick = e => setState({ ...state, expanded: !state.expanded })
  const handleClick = e => setState({ ...state, anchorEl: e.currentTarget })
  const handleClose = e => setState({ ...state, anchorEl: null })

  const handleLike = e => {
    if (userInfo.userId === null) {
      setMessageFunc("Авторизуйтесь, пожалуйста :с")
      return
    }
    setState({ ...state, voted: !state.voted })
    fetch(`${properties.address}/like`,
      {
        headers: {
          'Content-Type': 'application/json'
        },
        method: 'POST',
        credentials: 'include',
        body: JSON.stringify({ surveyId: props.surveyJson.id })
      })
      .then(response => {
        if (response.status === 403) {
          console.log("403")
          setState({ ...state, voted: props.surveyJson.voted })
        }
        return response.json()
      })
      .then(json => {
        if (json.result === 'Added') {
          setState({ ...state, likes: json.likes, voted: true })
        }
        else if (json.result === 'Deleted') {
          setState({ ...state, likes: json.likes, voted: false })
        }
      })
  }

  const handleDelete = e => {
    fetch(`${properties.address}/deleteSurvey`,
      {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ surveyId: props.surveyJson.id }),
        redirect: 'follow',
        credentials: 'include',
      })
      .then(response => {
        if (response.status === 200)
          setMessageFunc("Удалено :)")
          if (props.onSurveyDeleteFunc)
            props.onSurveyDeleteFunc()
      })
  }
  const setCommentsLength = count => {
    setState({ ...state, commentsCount: count })
  }
  return <>
    <Menu
      id="simple-menu"
      anchorEl={state.anchorEl}
      keepMounted
      open={Boolean(state.anchorEl)}
      onClose={handleClose}
    >
      <MenuItem onClick={() => { handleClose(); handleDelete(); }} >Удалить</MenuItem>
    </Menu>
    <Card>
      <CardHeader className={classes.header}
        avatar={<Avatar variant="square" className={classes.avatar} />}

        action={userInfo.userId === props.surveyJson.userId ? <IconButton onClick={handleClick} size={properties.iconSize} edge="start"><MoreVertIcon /></IconButton> : ""}

        subheader={<Link
          to={`${properties.repo}/survey/${props.surveyJson.id}`}
          style={{ textDecoration: 'none' }}>
          {props.surveyJson.date}</Link>}
        classes={{ action: classes.action }}
      />
      <CardContent style={{ padding: "0px" }}>
        {/* {Children.map(props.children, child => cloneElement(child, { flipped: state.flipped, votedCallback: votedCallback }))} */}
        {Children.map(props.children, child => cloneElement(child, { flipped: state.flipped }))}
      </CardContent>
      <CardActions disableSpacing>
        <Tooltip title={props.Answered ? 'Вы проголосовали' : 'Вы не проголосовали'}>
          <IconButton size={properties.iconSize} className={classes.icon}>
            <CheckIcon color={props.Answered ? 'secondary' : 'primary'} />
          </IconButton>
        </Tooltip>
        {state.answeredAmount > 0 && state.answeredAmount}
        <IconButton size={properties.iconSize} className={classes.icon} onClick={handleLike}>
          <FavoriteIcon color={state.voted ? 'secondary' : 'primary'} />
        </IconButton>
        {state.likes > 0 && state.likes}
        {state.answered &&
          <IconButton size={properties.iconSize} onClick={handleFlip}> <LoopIcon /> </IconButton>
        }
        {props.surveyJson.tags.map(tag =>
          <div className={classes.tag}>{tag}</div>
        )}
        <Typography style={{ marginLeft: "auto", marginRight: 5, marginBottom: 0 }} paragraph>Комментарии ({state.commentsCount})</Typography>
        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: state.expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={state.expanded}
        >
          <ExpandMoreIcon />
        </IconButton>
      </CardActions>

      <CommentsComponent surveyJson={props.surveyJson}
        expanded={state.expanded}
        setCommentsLength={setCommentsLength}
      />
    </Card>
  </>
}
