import React from 'react'
import {
    ComposableMap,
    Geographies,
    Geography,
    ZoomableGroup,
    Marker
} from "react-simple-maps";
import ReactTooltip from "react-tooltip";

export default function MapChart(props) {
    const [content, setContent] = React.useState("");
    const [markers, setMarkers] = React.useState(props.markers)
    const geoUrl =
        "https://raw.githubusercontent.com/mtraynham/natural-earth-topo/master/topojson/ne_110m_admin_0_countries.json"

    return (<>
        <ComposableMap data-tip="" projection="geoEqualEarth"   height="300"
            projectionConfig={{
                // rotate: [-20.0, -52.0, 0],
                // scale: 1000
            }}>
            <ZoomableGroup zoom={3} center={[50, 50]}>
                <Geographies geography={geoUrl}>
                    {({ geographies }) =>
                        geographies.map(geo => (
                            <Geography
                                onMouseEnter={() => {
                                    const { name } = geo.properties;
                                    setContent(`${name}`);
                                }}
                                onMouseLeave={() => {
                                    setContent("");
                                }}
                                style={{
                                    default: {
                                        fill: "#D6D6DA",
                                        outline: "none"
                                    },
                                    hover: {
                                        fill: "#F53",
                                        outline: "none"
                                    },
                                    pressed: {
                                        fill: "#E42",
                                        outline: "none"
                                    }
                                }}
                                key={geo.rsmKey}
                                geography={geo}
                                fill="#DDD"
                                stroke="#FFF"
                            />
                        ))
                    }
                </Geographies>
                {props.markers && props.markers.map(marker =>
                    <Marker coordinates={[marker.lo, marker.la]}>
                        <circle r={2} fill="#F53" />
                    </Marker>
                )}
            </ZoomableGroup>
        </ComposableMap>
        <ReactTooltip>{content}</ReactTooltip>
    </>)
}