import React, { useState, useEffect } from 'react';
import ReactMarkdown from 'markdown-to-jsx'
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import Typography from '@material-ui/core/Typography';
import { Paper } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import MainMenuComponent from "../MainMenuComponent"

const useStyles = makeStyles((theme) => ({
    markdown: {
        ...theme.typography.body2,
        padding: theme.spacing(3, 0),
    },
}));
export default function NewsPageComponent(props) {
    const classes = useStyles();
    const [posts, setPosts] = useState([])
    const importAll = r => r.keys().map(r);
    console.log("importing...")
    const markdownFiles = importAll(require.context('../../news', false, /\.md$/))
        .sort()
        .reverse()

    React.useEffect(() => {
        async function fetchData() {
            const posts = await Promise.all(markdownFiles.map((file) => fetch(file).then((res) => res.text())))
                .catch((err) => console.error(err))
            setPosts(posts)
        }
        fetchData()
    }, [])

    const renderers = {
        overrides: {
            p: { component: Typography, props: { paragraph: true } },
            li: { component: Typography, props: { variant: "subtitle1", component: "li" } },
            h4: { component: Typography, props: { variant: "h6" } }
        }
    }
    return (
        <>
            <Card>
                {posts.map((post, idx) => (
                    <>

                        <ReactMarkdown options={renderers}>

                            {
                                // <Typography> post </Typography>
                                post
                            }
                        </ReactMarkdown>
                        <hr style={{ borderTop: "1px dotted black", backgroundColor: "transparent", height: "1px" }} />
                    </>))}
            </Card>
        </>


    )
}