import React from 'react';

import Card from '@material-ui/core/Card';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { properties } from '../Properties/Properties.js'
import DraftsIcon from '@material-ui/icons/Drafts';
import { useHistory } from "react-router-dom";
import { Link } from 'react-router-dom';
import { NavLink } from 'react-router-dom';
import ContactSupportIcon from '@material-ui/icons/ContactSupport';
import HomeIcon from '@material-ui/icons/Home';
import FreeBreakfastIcon from '@material-ui/icons/FreeBreakfast';
import UpdateIcon from '@material-ui/icons/Update';
import MuiListItem from "@material-ui/core/ListItem";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import AcUnitIcon from '@material-ui/icons/AcUnit';
import { UserContext } from '../index'
import { PopupContext } from '../index'
import { LoginContext } from '../index'
import Collapse from '@material-ui/core/Collapse';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import InfoIcon from '@material-ui/icons/Info';


const useStyles = makeStyles((theme) => ({

    list: {
        padding: 0
    },
    checkbox: {
        padding: 3
    },
    inactive: {
        color: theme.palette.grey[300],
        textDecoration: "none"
    },
    menu: {
        backgroundColor: "#4c546391"
    },
    icon: {
        color: theme.palette.grey[300],
    },
    root: {
        "&:hover": {
            // backgroundColor: theme.palette.grey[700],
            background: "linear-gradient(21deg, rgba(54,54,61,0.5723476890756303) 0%, rgba(159,181,185,0.5639443277310925) 100%)"
            // backgroundColor: "linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%)"
        },
        color: theme.palette.common.white,
    },
    rotate: {
        // transform: "rotate(70deg)"
        animation: `spin 15s linear infinite`
    }

}))
export default function MainMenuComponent(props) {
    const setMessageFunc = React.useContext(PopupContext)
    const userInfo = React.useContext(UserContext)
    const classes = useStyles()
    let history = useHistory();
    const checkLoginStatus = React.useContext(LoginContext)
    const signOut = e => {
        fetch(`${properties.address}/signout`,
            {
                method: 'POST',
                credentials: 'include',
            })
            .then(response => {
                if (response.status === 200) {
                    // props.CheckLoginStatus()
                    checkLoginStatus()
                    setMessageFunc("Вы вышли :(")

                }
            })
    }
    const [state, setState] = React.useState({
        links: [{
            name: "Новогодние",
            link: `random?tag=newyear`,
            icon: <AcUnitIcon className={classes.rotate} />,
        }, {
            name: "Домашняя",
            // link: `${properties.repo}/home`,
            link: `home`,
            icon: <HomeIcon />,
        }, {
            name: "Рандом",
            // link: `${properties.repo}/random`,
            link: `random`,
            icon: <ContactSupportIcon />,
        }, {
            name: "Купить кофе",
            link: `donate`,
            icon: <FreeBreakfastIcon />,
        }, {
            name: "История изменений",
            link: `news`,
            icon: <UpdateIcon />,
        }, {
            name: "О сайте",
            link: `about`,
            icon: <InfoIcon />
        }],
        profileLinks: [{
            name: "Профиль",
            link: `profile`,
            icon: null,
        }, {
            name: "Создать",
            link: `createSurvey`,
            icon: null,
        }, {
            name: "Выйти",
            link: `signout`,
            icon: null,
            callback: signOut
        }],
        // selectedIndex: 0,
    })

    const handleClick = () => {
        setState({ ...state, open: !state.open })
    }

    return <>
        <Card className={classes.menu}>
            <List disablePadding dense>
                {state.links.map((link, index) => <>
                    <Link to={`${properties.repo}/${link.link}`}
                        className={classes.inactive}
                        onClick={e => setState({ ...state, selectedIndex: index })}
                    >
                        <ListItem
                            button
                            selected={`${window.location.pathname}${window.location.search}` === `${properties.repo}/${link.link}`}
                            className={classes.root}
                        >
                            <ListItemIcon
                                className={classes.icon}>
                                {link.icon}
                            </ListItemIcon>
                            <ListItemText primary={link.name} />
                        </ListItem>
                    </Link>
                </>
                )}
                {!userInfo.loggedIn && <Link to={`${properties.repo}/login`}
                    className={classes.inactive}
                >
                    <ListItem
                        button
                        selected={`${window.location.pathname}${window.location.search}` === `${properties.repo}/login`}
                        className={classes.root}
                    >
                        <ListItemText primary={"Sign In / Sign Up"} />
                    </ListItem>
                </Link>}
                {userInfo.loggedIn && <><ListItem className={classes.root} button onClick={handleClick}>

                    <ListItemIcon
                        className={classes.icon}>
                        <AccountCircleIcon />
                    </ListItemIcon>
                    <ListItemText primary="Профиль" />
                    {state.open ? <ExpandLess /> : <ExpandMore />}
                </ListItem>
                    <Collapse in={state.open} timeout="auto" unmountOnExit>
                        <List disablePadding dense>
                            {state.profileLinks.map((link, index) =>
                                <Link to={`${properties.repo}/${link.link}`}
                                    className={classes.inactive}
                                >
                                    <ListItem
                                        button
                                        selected={`${window.location.pathname}${window.location.search}` === `${properties.repo}/${link.link}`}
                                        className={classes.root}
                                        onClick={link.callback}
                                    >
                                        <ListItemText primary={link.name} />
                                    </ListItem>
                                </Link>)}
                        </List>
                    </Collapse></>}
            </List>
        </Card>
    </>
}