import React from 'react';
import TabPanelComponent from './TabPanelComponent'

import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import Tooltip from '@material-ui/core/Tooltip';
import Paper from '@material-ui/core/Paper';
import IconButton from '@material-ui/core/IconButton';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom'

import FormatListBulletedIcon from '@material-ui/icons/FormatListBulleted';
import ViewListIcon from '@material-ui/icons/ViewList';
import AppsIcon from '@material-ui/icons/Apps';
import TestComponent from './TestComponent'
import { useHistory } from "react-router-dom";
import { properties } from '../Properties/Properties.js';
import { PopupContext } from '../index'
import InfiniteScroll from './InfiniteScroll'

const useStyles = makeStyles((theme) => ({
  tabs: {
    backgroundColor: theme.palette.secondary.main,
  },
  mainBox: {
    backgroundColor: "transparent",
  },
  indicator: {
    backgroundColor: 'white',
  }
}))

export default function TabsComponent(props) {

  const history = useHistory()
  const setMessageFunc = React.useContext(PopupContext)
  const classes = useStyles()
  const path = history.location.pathname.split("/").pop()
  const [state, setState] = React.useState({
    value: path === "new" || path === "home" ? 1 : 0,
    activeType: "",
    reloadFunc: undefined,
  })

  const handleChange = (event, newValue) => {
    setState({ ...state, value: newValue })
  }

  const handleFilter = type => {
    let newType = state.activeType === type ? "" : type
    setState({ ...state, activeType: newType })
    state.reloadFunc(undefined, newType)
  }

  const changeReloadFunc = load => {
    setState({ ...state, reloadFunc: load })
  }

  return <Box style={{ paddingLeft: "20px", paddingRight: "20px", paddingTop: "20px" }}>
    <Grid container spacing={3} alignItems="flex-start" justify="flex-start">
      <Grid item xs={12}>
        <Paper className={classes.tabs}>
          <Tabs TabIndicatorProps={{ style: { backgroundColor: 'white' } }} selectionFollowsFocus centered value={state.value}  >
            <Tab onClick={e => handleChange(e, 0)} component={Link} to={`${properties.repo}/top`} label="TOP" />
            <Tab onClick={e => handleChange(e, 1)} component={Link} to={`${properties.repo}/new`} label="NEW" />

            <Tooltip title="Единичный выбор">
              <IconButton
                color={state.activeType === "SINGLE" ? "default" : "primary"}
                onClick={e => { handleFilter("SINGLE") }}
              >
                <ViewListIcon />
              </IconButton>
            </Tooltip>
            <Tooltip title="Множественный выбор">
              <IconButton
                color={state.activeType === "MULTIPLE" ? "default" : "primary"}
                onClick={e => { handleFilter("MULTIPLE") }}
              >
                <FormatListBulletedIcon />
              </IconButton>
            </Tooltip>
            <Tooltip title="Рейтинговый выбор">
              <IconButton
                color={state.activeType === "RATING" ? "default" : "primary"}
                onClick={e => { handleFilter("RATING") }}
              >
                <AppsIcon />
              </IconButton>
            </Tooltip>
          </Tabs>
        </Paper>
      </Grid>
      <Grid item xs={12} id="surveys">
        <Paper className={classes.mainBox}>
          <Box  >
            {state.value == 0 && <InfiniteScroll ActiveType={state.activeType} SetReloadFunction={changeReloadFunc} Mode="Top" />}
            {state.value == 1 && <InfiniteScroll ActiveType={state.activeType} SetReloadFunction={changeReloadFunc} Mode="New" />}
          </Box>
        </Paper>
      </Grid>
    </Grid>
  </Box>
}