import React from 'react';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import Button from '@material-ui/core/Button';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';
import DefaultTooltipContent from 'recharts/lib/component/DefaultTooltipContent';
import { makeStyles, withStyles } from "@material-ui/core/styles";
import { KeyPressedContext } from './Pages/RandomPageComponent'

const useStyles = makeStyles((theme) => ({

  bar: {
    color: "red",
    fontSize: "smaller"
  },
}))

export default function SingleSurveyComponent(props) {
  const f = props
  props = props.props
  let keyUpEvent = React.useContext(KeyPressedContext)
  // let key = React.useContext(KeyPressedContext)
  const [state, setState] = React.useState({
    error: false,
    voted: props.surveyJson.answered,
    choosedOption: "",
    helperText: props.HelperText,
    chart: undefined,
    options: props.surveyJson.options.map(option => ({ name: option, chart: <br /> })),
    keyPressed: null
  })
  const classes = useStyles()

  const handleSubmit = e => {
    if (state.voted)
      return
    if (e)
      e.preventDefault()
    let submitOptions = {
      "surveyId": props.surveyJson.id,
      "choosedOption": state.choosedOption
    }
    props.handleSubmit(e, submitOptions, validate, processDataFunc)
  }

  const validate = () => {
    if (state.choosedOption === "") {
      setState({ ...state, error: true, helperText: "Выберите вариант" })
      return false
    }
    setState({ ...state, error: false, helperText: props.HelperText });
    return true
  }

  // Example of stats data
  // const data = [
  //   {
  //     "name": "opt1",
  //     "value": 30,
  //     "count": 54
  //   }]
  const processDataFunc = (e, result) => {
    let longestOption = result.map(elem => elem.name).sort((e1, e2) => e2.length - e1.length)[0].length
    let choosedOption = props.surveyJson.choosedOption || state.choosedOption

    state.options.forEach(option => {
      let data = result.filter(opt => opt.name === option.name)
      option.name = option.name
      option.value = data[0].value
      let chart = <BarChart

        width={14 * longestOption + 200}
        height={20}
        data={data}
        barSize={10}
        layout="vertical">
        <XAxis type="number" hide />
        <YAxis
          // height={10}
          hide
          tick={props => {
            return <text
              x={props.x}
              y={props.y}
              textAnchor="end"
              fontWeight={props.payload.value === choosedOption ? "bold" : "normal"}
              fill={props.payload.value === choosedOption ? "blue" : "black"}
            >
              {props.payload.value + " (" + result.filter(r => r.name === props.payload.value)[0].value + "%)"}
            </text>
          }}
          type="category" dataKey="name" width={14 * longestOption} />
        <Tooltip content={props => {
          if (props.payload[0] != null) {
            const newPayload = [{
              name: 'голосов',
              value: props.payload[0].payload.count
            }]
            return <DefaultTooltipContent {...props} payload={newPayload} />
          }
          return <DefaultTooltipContent {...props} />
        }} />
        <Bar background={{ fill: '#eee' }} dataKey="value" fill="#8884d8" />
      </BarChart>
      option.chart = chart
    })

    setState({ ...state, options: state.options, voted: true })
  }

  const handleChange = e => {
    if (state.voted)
      return
    setState({ ...state, choosedOption: e.target.value })
  }

  React.useEffect(() => {
    if (props.Answered)
      props.getStats(undefined, processDataFunc)
  }, [props.Answered])

  React.useEffect(() => {
    if (keyUpEvent)
      keyUpEvent = keyUpEvent.keyCode 
    if (keyUpEvent >= 49 && keyUpEvent <= 49 + state.options.length - 1) {
      setState({ ...state, choosedOption: state.options[keyUpEvent - 49].name })
    }
    if (keyUpEvent === 13){
      handleSubmit()
    }
  }, [keyUpEvent])

  React.useEffect(() => {
    setState({ ...state, choosedOption: "", error: false })
  }, [])

  return <FormControl
    component="fieldset"
    error={state.error}
  >
    {/* {props.flipped &&
    <StatisticsComponent
      Options={props.surveyJson.options}
      Type="Multiple"
      SurveyId={props.surveyJson.surveyId}>
    </StatisticsComponent>} */}
    {!props.flipped && <form onSubmit={handleSubmit}>
      <FormLabel component="legend">{props.surveyJson.question}</FormLabel>
      {/* {!props.Answered && <RadioGroup value={state.choosedOption} onChange={handleChange}>
    
        {props.surveyJson.options.map(option =>
          <>
            <FormControlLabel
              value={option}
              control={<Radio />}
              label={option}
            />
          test
          </>
        )}  */}
      <RadioGroup value={props.surveyJson.choosedOption ? props.surveyJson.choosedOption : state.choosedOption} onChange={handleChange}>

        {state.options.map((option, index) =>
          <>
            <FormControlLabel
              value={option.name}
              control={<Radio />}
              label={<div style={{ color: state.choosedOption === option.name ? "blue" : "black" }}>{option.name}{state.voted ? ` (${option.value}%)` : ""}</div>}
            />
            <div className={classes.bar}>
              {option.chart}
            </div>
          </>
        )}
      </RadioGroup>
      {/* {props.Answered && state.chart} */}
      <FormHelperText>{state.helperText}</FormHelperText>
      {!state.voted && <Button type="submit" variant="contained" color="primary">Узнать</Button>}
    </form>
    }
    {/* {state.voted && !props.Flipped && 
    <StatisticsComponent 
    Options={this.props.Options} 
    Type="Single" 
    SurveyId={this.SurveyId} />} */}
  </FormControl >
}