import React from 'react';

import Grid from '@material-ui/core/Grid';
import { properties } from '../Properties/Properties.js';
import MultipleSurveyComponent from "./MultipleSurveyComponent";
import SingleSurveyComponent from "./SingleSurveyComponent";
import RatingSurveyComponent from "./RatingSurveyComponent";
import CardComponent from "./CardComponent";
import { useHistory } from "react-router-dom";
import { PopupContext } from '../index'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContentText from '@material-ui/core/DialogContentText';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  surveyCard: {
    "&:focus": {
      outline: "none"
    }
  },
}))

export default function BaseSurveyComponent(props) {
  const classes = useStyles()
  const history = useHistory()
  const img = props.surveyJson.image;
  const setMessageFunc = React.useContext(PopupContext)
  const [state, setState] = React.useState({
    helperText: "Ответь сам, чтобы узнать мнения других",
    answered: props.surveyJson.answered,
    open: false,
    // choosedIndex: -1,
    pressed: false
  })

  const handleClose = opened => {
    setState({ ...state, open: opened })
  }

  const getStats = (e, processDataFunc) => {
    fetch(`${properties.address}/getStats?surveyId=${props.surveyJson.id}`,
      {
        method: 'GET',
        redirect: 'follow',
        credentials: 'include'
      })
      .then(response => {
        if (response.status === 200) {
          response.json().then(result => {
            processDataFunc(e, result)
            // setFetchedData(result)
          })
        }
      }
      )
  }

  const handleSubmit = (e, submitOptions, validatingFunc, processDataFunc) => {
    if (!validatingFunc())
      return

    fetch(`${properties.address}/answerSurvey`,
      {
        method: 'POST',
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify(submitOptions),
        redirect: 'follow',
        credentials: 'include'
      })
      .then(response => {
        if (response.status === 200) {
          // props.votedCallback()
          console.log('base: answered')
          setState({ ...state, answered: true })
        }
        else
          props.History.push('/login')
      }
      )
  }
  
  const params = { 
    "pressed": state.pressed, 
    "choosedIndex": state.choosedIndex, 
    "getStats": getStats, 
    "Answered": state.answered, 
    "handleSubmit": handleSubmit, 
    "surveyJson": props.surveyJson, 
    "HelperText": state.helperText, 
    "History": history ,
    "onSurveyDeleteFunc": props.onSurveyDeleteFunc,
    "openMap": props.openMap,
  }
  return <>
    {/* {returnBody()} */}
    <CardComponent
      Answered={state.answered}
      surveyJson={props.surveyJson}
      onSurveyDeleteFunc={props.onSurveyDeleteFunc}
    >
      <Grid
        tabindex={0}
        id = "surveyCard"
        className={classes.surveyCard} container >
        <Grid item xs={img === '' || img === null ? 0 : 4}>
          <img onClick={() => handleClose(true)} alt="" style={{ width: "100%", maxWidth: "200px", cursor: "pointer" }} src={img}></img>
        </Grid>
        <Grid item xs={img === "" || img === null ? 12 : 8}>
          {props.surveyJson.type === "Single" && <SingleSurveyComponent props={params} />}
          {props.surveyJson.type === "Multiple" && <MultipleSurveyComponent props={params} />}
          {props.surveyJson.type === "Rating" && <RatingSurveyComponent props={params} />}
        </Grid>
      </Grid>
      <Dialog
        open={state.open}
        onClose={() => { handleClose(false) }}
      >
        <IconButton style={{ position: "absolute", right: "0" }} onClick={() => { handleClose(false) }}>
          <CloseIcon />
        </IconButton>
        <img alt="" style={{ width: "100%" }} src={img}></img>
      </Dialog>
    </CardComponent >
  </>
}